import React, { createRef, useContext, useEffect, useMemo, useRef } from 'react'
import { useWindowSize } from 'react-use'
import { get } from 'lodash'
import { IoTriangle } from 'react-icons/io5'
import gsap, { TimelineMax, TweenMax } from 'gsap'
import ScrollMagic from "scrollmagic";
import { ScrollMagicPluginGsap } from "scrollmagic-plugin-gsap";

import Container from '../../components/Container'
import Box from '../../components/Box'
import Flex from '../../components/Flex'
import FullpageHeight from '../../components/FullpageHeight'
import Text from '../../components/Text'
import Button from '../../components/Button'
import BackgroundImage from '../../components/BackgroundImage'
import { responsive } from '../../components/ThemeProvider/theme';

import useResponsive from '../../contexts/mediaQuery/useResponsive'

import Grid from '../CataloguePage/Grid'
import useData from '../TrashPage/data/useData'
import useShowHeader from '../../contexts/header/useShowHeader';
import containerWidthContext from '../../contexts/containerWidth/context';

import trashes from './trashes.png'
import trashesHighlight from './highlight-trashes.png'
import title from './title.svg'
import titleOverlay from './title-overlay.svg'
import bubble from './bubble.svg'
import { ReactComponent as Charts } from './popup-charts.svg'
import Face from '../Face'

ScrollMagicPluginGsap(ScrollMagic, TweenMax, TimelineMax);

const TriangleDown = props => <IoTriangle style={{ transform: 'rotate(180deg)' }} {...props} />

const trashRatio = 2960 / 1468
const titleRatio = 1686.44 / 317.89

let controller
let timeline1
let timeline2

const ScrollButton = (props) => (
  <Button
    className="scroll-btn"
    bg="white"
    rounded="full"
    fontSize={responsive('3em', '1.5em')}
    rightIcon={<TriangleDown />}
    px="1em"
    {...props}
  />
  )

const HomePage = () => {
  useShowHeader('colors.yellow')
  const { isMobile } = useResponsive()
  const heroTrashRef = useRef()
  const bubbleRef = useRef()
  const trashMountRef = useRef()
  const data = useData()
  const { containerWidth } = useContext(containerWidthContext)
  const windowSize = useWindowSize()
  const trashPx = useMemo(() => {
    if (containerWidth / trashRatio > windowSize.height * 0.6) {
      return ((containerWidth / trashRatio) - windowSize.height * 0.6) / 2
    }
    return 0
  }, [containerWidth, windowSize.height])

  const pages = useMemo(() => [
    <>
      <Container pt="2em" textAlign="center" height="100%" position="relative">
        <BackgroundImage src={title} ratio={titleRatio}>
          <Box.FullAbs zIndex={4}>
            <BackgroundImage src={titleOverlay} ratio={titleRatio} />
          </Box.FullAbs>
        </BackgroundImage>
        <Box.Absolute bottom="4em" left="0" right="0" textAlign="center" zIndex={2}>
          <ScrollButton>往下滑走進垃圾堆 聽聽垃圾的心聲</ScrollButton>
        </Box.Absolute>
      </Container>
    </>,
    <>
      <Container height="100%" position="relative">
        <Box.Absolute bottom="4em" left="2em" zIndex={2}>
          <ScrollButton>往下滑看看難丟的三個等級</ScrollButton>
        </Box.Absolute>
      </Container>
    </>,
    <>
      <Container height="100%" position="relative">
        <Box.Absolute bottom="4em" right="2em" zIndex={2}>
          <ScrollButton>往下滑看看他們的同伴有誰</ScrollButton>
        </Box.Absolute>
      </Container>,
    </>,
    <Grid />,
  ], [])
  const pageRefs = useMemo(() => pages.map(() => createRef()), [pages])
  useEffect(() => {
    if (controller) {
      timeline1.kill()
      timeline2.kill()
      controller.destroy()
    }
    const trashWidth = (containerWidth - trashPx * 2)
    controller = new ScrollMagic.Controller();
    timeline1 = new TimelineMax()
    timeline2 = new TimelineMax()
    timeline1.to(heroTrashRef.current, {
      scale: 1.5,
      left: trashWidth * 0.58,
      top: windowSize.height - (trashWidth / trashRatio),
    }, 0)
    timeline1.to(bubbleRef.current, {
      opacity: 1,
    }, 0)
    timeline1.to(trashMountRef.current, { scale: 1.5, x: trashWidth * 0.66, y: '-75%' }, 0)
    new ScrollMagic.Scene({
      triggerElement: pageRefs[1].current,
      triggerHook: '13.75%',
      duration: '75%',
    })
    .setTween(timeline1)
    .addTo(controller)
    timeline2.to(trashMountRef.current, {
      left: `-=${trashWidth * 3}`,
    }, 0)
    timeline2.to(heroTrashRef.current, {
      left: `-=${trashWidth * 1.5}`,
    }, 0)
    timeline2.to(trashMountRef.current.querySelector('.trash-mount'), {
      opacity: 0.3,
    }, 0)
    timeline2.to(trashMountRef.current.querySelector('.trash-chart'), {
      opacity: 1,
    }, 0)
    new ScrollMagic.Scene({
      triggerElement: pageRefs[2].current,
      triggerHook: '33%',
      duration: '66%',
    })
    .setTween(timeline2)
    .addTo(controller)

    pages.forEach((p, i) => {
      if (i) {
        new ScrollMagic.Scene({
          triggerElement: pageRefs[i - 1].current,
          triggerHook: 'onLeave',
          duration: '25%',
        })
        .setTween(pageRefs[i - 1].current.querySelector('.scroll-btn'), 1, { opacity: 0 })
        .addTo(controller)
      }
    })
  }, [containerWidth])
  return (
    <div>
      {pages.map((page, i) => (
        <FullpageHeight bg="colors.yellow" key={i} ref={pageRefs[i]}>
          {page}
        </FullpageHeight>
      ))}
      <Box.Fixed left="0" top="107vh" right="0" transform="translateY(-100%)" ref={trashMountRef} transformOrigin="center bottom">
        <Container px={`${trashPx}px`}>
          <Box.Relative>
            <BackgroundImage src={trashes} ratio={trashRatio} className="trash-mount" />
            <Box.FullAbs>
              <BackgroundImage src={trashesHighlight} ratio={trashRatio} />
            </Box.FullAbs>
            <Box.Absolute width="100%" left="12%" top="0" className="trash-chart" opacity="0">
              <Charts />
            </Box.Absolute>
          </Box.Relative>
        </Container>
      </Box.Fixed>
      <FullpageHeight position="fixed" left="0" right="0">
        <Container position="relative" height="100%">
          <Box.Absolute left="-6%" top="0" width="36%" ref={heroTrashRef} transform="translate3d(0, -34%, 0)">
            <BackgroundImage src={get(data, [1, 'img'])} ratio={850 / 624} transform="rotate(7deg) ">
              <Face id="1" />
            </BackgroundImage>
            <Box.Absolute width="90%" left="-55%" top="10%" ref={bubbleRef} opacity="0">
              <BackgroundImage src={bubble} ratio={1006 / 542}>
                <Box.Absolute left="4em" top="4em" right="3em">
                  <Text fontWeight="900" fontSize="1.25em">
                    欸新來的！看你穿得人模人樣<br />
                    肯定是和我們一樣超難丟，<br />
                    才被亂丟到這吧<br />
                    但要比難丟，我們可是競爭得很！
                  </Text>
                </Box.Absolute>
              </BackgroundImage>
            </Box.Absolute>
          </Box.Absolute>
          {/* <Box.Relative>
            <Box.Absolute top="2em" left="0" right="0">
              <BackgroundImage src={titleOverlay} ratio={titleRatio} />
            </Box.Absolute>
          </Box.Relative> */}
        </Container>
      </FullpageHeight>
    </div>
  )
}

export default HomePage
